code.custom = `
    <template>
        <Form ref="form3" :value="form3" :rules="rule3" :label-width="80">
            <FormItem label="" name="passwd">
                <FormInput type="password" v-model="form3.passwd"></FormInput>
            </FormItem>
            <FormItem label="" name="passwdCheck">
                <FormInput type="password" v-model="form3.passwdCheck"></FormInput>
            </FormItem>
            <FormItem label="" name="age">
                <FormInput type="text" v-model="form3.age" number></FormInput>
            </FormItem>
            <FormItem>
                <BaseButton type="primary" @on-click-btn="handleSubmit('form3')"></BaseButton>
                <BaseButton type="ghost" style="margin-left: 8px" @on-click-btn="handleReset('form3')"></BaseButton>
            </FormItem>
        </Form>
    </template>
    export default {
        data() {
        const validatePass = (rule, value, callback) => {
            if (value === '') {
                callback(new Error(''));
            } else {
                if (this.form3.passwdCheck !== '') {
                    // 
                    this.$refs.form3.validateField('passwdCheck');
                }
                callback();
            }
        };
        const validatePassCheck = (rule, value, callback) => {
            if (value === '') {
                callback(new Error(''));
            } else if (value !== this.form3.passwd) {
                callback(new Error('!'));
            } else {
                callback();
            }
        };
        const validateAge = (rule, value, callback) => {
            if (!value) {
                return callback(new Error(''));
            }
            // 
            setTimeout(() => {
                if (!Number.isInteger(value)) {
                    callback(new Error(''));
                } else {
                    if (value < 18) {
                        callback(new Error('18'));
                    } else {
                        callback();
                    }
                }
            }, 1000);
        };
        return {
            form3: {
                passwd: '',
                passwdCheck: '',
                age: ''
            },
            rule3: {
                passwd: [
                    { validator: validatePass, trigger: 'blur' }
                ],
                passwdCheck: [
                    { validator: validatePassCheck, trigger: 'blur' }
                ],
                age: [
                    { validator: validateAge, trigger: 'blur' }
                ]
            },
        };
        },
        methods: {
            handleSubmit (name) {
                this.$refs[name].validate((valid) => {
                    if (valid) {
                        console.log("success");
                    } else {
                        console.log("fail");
                    }
                })
            },
            handleReset (name) {
                this.$refs[name].resetFields();
            }
        },
        mounted() {}
    };
`
export default code
