let code = {}

code.html = `
    <Sider :sider="sider"></Sider>
`

code.js = `
/**
     * sider
     * @param {object} list - 
     *                 name - 
     *                 href - url
     *                 icon - icon
     *                 module - /
     *                 slide - true-false-
     *                 list - 
     * @param {object} cur - 
     *                 module - /
     *                 href - url
     * @param {object} slideIcon - icon
     *                 up - 
     *                 down - 
     * 
     */
    sider: {
        list: [
            {
                name: "1",
                href: "/menu1",
                icon: "menu1",
            },
            {
                name: "2",
                icon: "menu2",
                module: "menu2",
                slide: true,
                list: [
                    {
                        name: "1",
                        href: "/menu2_1",
                        module: "menu2",
                    },
                    {
                        name: "2",
                        href: "/menu2_2",
                        module: "menu2"
                    }
                ]
            },
            {
                name: "3",
                icon: "menu3",
                module: "menu3",
                slide: false,
                list: [
                    {
                        name: "1",
                        href: "/menu3_1",
                        module: "menu3",
                    },
                    {
                        name: "2",
                        href: "/menu3_2",
                        module: "menu3"
                    },
                    {
                        name: "3",
                        href: "/menu3_2",
                        module: "menu3"
                    }
                ]
            }
        ],
        cur:{
            module: "menu2",
            href: "/menu2_2"
        },
        slideIcon:{
            up: "slideUp",
            down: "slideDown"
        }
    }
`
export default code
