let code = {}

code.base = `
    <template>
        <BaseButton type="primary" @on-click-btn="modal1=true"></BaseButton>
        <BaseButton type="primary" @on-click-btn="modal2=true"></BaseButton>
        <Modal v-model="modal1" maskClosable>
            <div class="demo-1"></div>
        </Modal>
        <Modal v-model="modal2" title="">
            <div slot="body">
                <P>1</P>
                <P>2</P>
            </div>
        </Modal>
    </template>
    export default {
        data() {
            return {
                modal1:false,
                modal2:false
            };
        },
        methods: {
        },
        mounted() {}
    };
`
code.close = `
    <template>
        <BaseButton type="primary" @on-click-btn="modal3=true"></BaseButton>
        <BaseButton type="primary" @on-click-btn="modal4=true"></BaseButton>
        <Modal v-model="modal3" title="" >
            <div slot="body">
                <P>1</P>
                <P>2</P>
            </div>
        </Modal>
        <Modal v-model="modal4" title="" :closable="false">
            <div slot="body">
                <P>1</P>
                <P>2</P>
            </div>
        </Modal>
    </template>
    export default {
        data() {
            return {
                modal3:false,
                modal4:false,
            };
        },
        methods: {
        },
        mounted() {}
    };
`


export default code
