let code = {}

code.base = `
    <template>
        <RadioGroup v-model="radio1">
            <Radio label="1">radio</Radio>
            <Radio label="2">radio</Radio>
        </RadioGroup>
    </template>
    export default {
        data() {
            return {
                radio1:'1'
            };
        },
    };
`

code.custom = `
    <template>
        <RadioGroup v-model="radio2" disabled>
            <Radio label="man"><Icon type="man"></Icon></Radio>
            <Radio label="woman"><Icon type="woman"></Icon></Radio>
        </RadioGroup>
    </template>
    export default {
        data() {
            return {
                radio2:'man'
            };
        },
    };
`

code.disabled = `
    <template>
        <RadioGroup v-model="radio1" disabled>
            <Radio label="1"></Radio>
            <Radio label="2"></Radio>
        </RadioGroup>
        <RadioGroup v-model="radio3" >
            <Radio label="1" disabled></Radio>
            <Radio label="2"></Radio>
            <Radio label="3"></Radio>
        </RadioGroup>
    </template>
    export default {
        data() {
            return {
                radio1:'1',
                radio3:'2
            };
        },
    };
`

code.type = `
    <template>
        <RadioGroup v-model="radio4" type="button">
            <Radio label="hour"></Radio>
            <Radio label="day"></Radio>
            <Radio label="month"></Radio>
            <Radio label="year"></Radio>
        </RadioGroup>
        <RadioGroup v-model="radio4" type="button">
            <Radio label="hour"></Radio>
            <Radio label="day" disabled></Radio>
            <Radio label="month"></Radio>
            <Radio label="year"></Radio>
        </RadioGroup>
        <RadioGroup v-model="radio4" type="button" disabled>
            <Radio label="hour"></Radio>
            <Radio label="day"></Radio>
            <Radio label="month"></Radio>
            <Radio label="year"></Radio>
        </RadioGroup>
    </template>
    export default {
        data() {
            return {
                radio4:'hour'
            };
        },
    };
`
code.direction = `
    <template>
        <RadioGroup v-model="radio2" vertical>
            <Radio label="man"><Icon type="man"></Icon></Radio>
            <Radio label="woman"><Icon type="woman"></Icon></Radio>
        </RadioGroup>
    </template>
    export default {
        data() {
            return {
                radio2:'man',
            };
        },
    };
`

export default code
