        const isLt2M = file.size / 1024 / 1024 < 2;

        if (!isJPG) {
            this.exceedTip_text = ` JPG !`;
            this.exceedTip = true;
        }
        if (!isLt2M) {
            this.exceedTip_text = ` 2MB!`;
