let code = {}

code.vueGet = `
/**
    * GET
    * @param {float}  vm - vue
    * @param {string} url - url
    * @param {object} params - 
    * @param {object} callback - 
    * @param {string} name - 
    * @param {object} sendPara - 
    * @param {object} failCallback - 
    * 
    *      window.domain: "/dataBrain"
    *      before(request)abort
    */
    vueGet: function(vm,url,params,callback,name,sendPara,failCallback){
        vm.$http.get(window.domain+url,{
            params: params,
            before(request) {
                if(name){
                    if (vm["previousRequest_"+name]) {
                        vm["previousRequest_"+name].abort();
                    }
                    vm["previousRequest_"+name] = request;
                }
            }
        }).then(d=>{            
            callback(d.body,(sendPara)?sendPara:'');
        },d=>{
            if(failCallback){
                failCallback(d.body);
            }
        });
    }
`

code.vuePost = `
/**
    * POST
    * @param {float}  vm - vue
    * @param {string} url - url
    * @param {object} params - 
    * @param {object} callback - 
    * @param {string} name - 
    * @param {object} sendPara - 
    * @param {object} failCallback - 
    * 
    *      window.domain: "/dataBrain"
    *      before(request)abort
    */
    vuePost:function(vm,url,params,callback,name,sendPara,failCallback){
        vm.$http.post(window.domain+url,params, {
            before(request) {
                if (vm["previousRequest_"+name]) {
                    vm["previousRequest_"+name].abort();
                }
                vm["previousRequest_"+name] = request;
            }
        }).then(d=>{            
            callback(d.body,(sendPara)?sendPara:'');
        },d=>{
            if(failCallback){
                failCallback(d.body);
            }
        });
    }
`

export default code
