let code = {}

code.base = `
    <FormInput v-model="input1" placeholder=""></FormInput>
    <FormInput v-model="input1" placeholder="" :width="500"></FormInput>
    <FormInput v-model="input1" placeholder="10" :width="300" :maxlength=10></FormInput>
    <FormInput v-model="input1" placeholder="" :width="300" readonly></FormInput>
`

code.type = `
    <FormInput v-model="input2.value" placeholder="" type="textarea"></FormInput>
    <FormInput v-model="input2.value" placeholder="" type="textarea" :width="500"></FormInput>
    <FormInput v-model="input2.value" placeholder="5" type="textarea" :width="300" :rows=5></FormInput>
    <FormInput v-model="input2.value" placeholder="" type="textarea" :width="300" autosize></FormInput>
    <FormInput v-model="input2.value" placeholder="" type="textarea" :width="300" :autosize="{minRows: 2, maxRows: 6}"></FormInput>
`

code.disabled = `
    <FormInput v-model="input1.value" placeholder="" :width="300" disabled></FormInput>
    <FormInput v-model="input1.value" placeholder="" type="textarea" :width="300" disabled></FormInput>
`

code.clear = `
    <FormInput v-model="input1.value" placeholder="" :width="300"  clearable></FormInput>
`
code.icon = `
    <FormInput v-model="input1.value" placeholder="" :width="300"  clearable pre-icon="search"></FormInput>
    <FormInput v-model="input1.value" placeholder="" :width="300"  clearable>
        <span slot="preIcon" class="custom-search">
            <Icon slot="preIcon" type="search"></Icon>
        </span>
    </FormInput>
    <FormInput v-model="input1" placeholder="" :width="300" :aft-icon="'eye-disabled'"></FormInput>
    <br/>
    <br/>
    <FormInput v-model="input1" placeholder="" :width="300" aft-icon="search"></FormInput>
`

code.size = `
    <FormInput v-model="input1.value" placeholder="" :width="300"  clearable :pre-icon="'search'" size="large"></FormInput>
    <FormInput v-model="input1.value" placeholder="" :width="300"  clearable :pre-icon="'search'"></FormInput>
    <FormInput v-model="input1.value" placeholder="" :width="300"  clearable :pre-icon="'search'" size="small"></FormInput>
`
code.fix =  `
    <FormInput v-model="input1.value" placeholder="">
        <span slot="prepend" class="custom-pre">http://</span>
        <span slot="append" class="custom-append">.com</span>
    </FormInput>
`

export default code
