                <BaseButton type="primary" @on-click-btn="handleLoading2">2.5</BaseButton>
                <BaseButton type="primary" @on-click-btn="handleLoading3"></BaseButton>
                <div class="q-title"></div>
                <div>
                    $Loading
                </div>
                <div class="q-title"></div>
                <DCode :code="code.full" lang="html"></DCode>
            </div>
            <div class="title2" id="title-6">
                API
            </div>
            <div class="wrap">
                <div class="q-title">Loadingprops</div>
                <Table type="bordered" :thead="api.thead" :data="api.data">
                </Table>
                <div class="q-title">Loadingslot</div>
                <Table type="bordered" :thead="slots.thead" :data="slots.data">
                </Table>
            </div>
        </div>
        <DMenu></DMenu>
    </div>
</template>

<script>
import Code from '../../../code/component/tip/loading';
export default {
  data() {
    return {
        code: Code,
        loading1:true,
        api:{
            thead:[
                {
                    column:'prop',
                    name:'',
                },
                {
                    column:'description',
                    name:'',
                },
                {
                    column:'type',
                    name:'',
                },
                {
                    column:'option',
                    name:'',
                },
                {
                    column:'default',
                    name:'',
                },
            ],
            data:[
                {
                    prop:'size',
                    description:'',
                    type:'String',
                    option:'large/small',
                    default:'-'
                },
                {
                    prop:'icon',
                    description:'',
                    type:'String',
                    option:'-',
                    default:'loading'
                },
                {
                    prop:'fix',
                    description:'',
                    type:'Boolean',
                    option:'-',
                    default:'false'
                }
            ]
        },
        slots:{
            thead:[
                {
                    column:'name',
                    name:'',
                },
                {
                    column:'description',
                    name:'',
                },
            ],
            data:[
                {
                    name:'',
                    description:'',
                },
                {
                    name:'icon',
                    description:'',
                }
            ]
        },
    };
  },
  methods: {
    handleLoading2 () {
        this.$Loading.show();
        setTimeout(() => {
            this.$Loading.hide();
        }, 2500);
    },
    handleLoading3 () {
        this.$Loading.show({
            render: (h) => {
                return h('div', [
                    h('Icon', {
                        'class': 'demo-icon-load',
                        props: {
                            type: 'load-c',
                            size: 18
                        }
                    }),
                    h('div', 'Loading')
                ])
            }
        });
        setTimeout(() => {
            this.$Loading.hide();
        }, 2500);
    }
  },
  mounted() {}
};
</script>
<style lang="scss" scoped>
.demo-loading-wrap{
    width: 100%;
    height: 200px;
    padding:90px 0;
    text-align: center;
    border:1px solid #ccc;
    position:relative;
    margin-bottom:20px;
}
.demo-icon-load{
    animation: rotating 2s linear infinite;
    -webkit-animation: rotating 2s linear infinite;
}
@keyframes rotating
{
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(1turn);
    }
}
</style>
