let code = {}

code.type = `
    <Alert type="info"></Alert>
    <Alert type="success"></Alert>
    <Alert type="warn"></Alert>
    <Alert type="error"></Alert>
`

code.mode = `
    <template>
        <BaseButton type="primary" @on-click-btn="alert1=true">info</BaseButton>
        <BaseButton type="primary" @on-click-btn="alert2=true">success</BaseButton>
        <BaseButton type="primary" @on-click-btn="alert3=true">warn</BaseButton>
        <BaseButton type="primary" @on-click-btn="alert4=true">error</BaseButton>
        <BaseButton type="primary" @on-click-btn="alert41=true">100px</BaseButton>
        <Alert type="info" mode="fix" v-model="alert1"></Alert>
        <Alert type="success" mode="fix" v-model="alert2"></Alert>
        <Alert type="warn" mode="fix" v-model="alert3"></Alert>
        <Alert type="error" mode="fix" v-model="alert4"></Alert>
        <Alert type="error" mode="fix" v-model="alert41" top="100" closable :auto-hidden="false"></Alert>
    </template>
    export default {
        data() {
            return {
                alert1:false,
                alert2:false,
                alert3:false,
                alert4:false,
                alert41:false,
            };
        },
    };
`
code.hidden = `
    <template>
        <BaseButton type="primary" @on-click-btn="alert5=true"></BaseButton>
        <BaseButton type="primary" @on-click-btn="alert6=true">10S</BaseButton>
        <Alert type="info" mode="fix" v-model="alert5" auto-hidden ></Alert>
        <Alert type="success" mode="fix" v-model="alert6" auto-hidden :duration=10></Alert>
    </template>
    export default {
        data() {
            return {
                alert5:false,
                alert6:false,
            };
        },
    };
`

code.close = `
    <Alert type="info" ></Alert>
    <Alert type="info" closable></Alert>
    <Alert type="info" closable><span slot="close"></span></Alert>
`

code.icon = `
    <Alert type="info" show-icon></Alert>
    <Alert type="success" show-icon></Alert>
    <Alert type="warn" show-icon></Alert>
    <Alert type="error" show-icon></Alert>
    <Alert show-icon icon="loading"></Alert>
    <Alert show-icon icon="checkmark-circled" icon-color="#36c626"></Alert>
    <Alert show-icon icon="android-alert" icon-color="#ff1a2e"></Alert>
`


export default code
