<template>
    <div class="other">
        <div class="title1">
            Form 
        </div>
        <div class="mItem">
            <div class="title2" id="title-1">
                
            </div>
            <div class="wrap">
                <Form ref="form1" :value="form1" :rules="rules1" :label-width=80>
                    <FormItem name="name" label="">
                        <FormInput v-model="form1.name" placeholder="" :width="300"></FormInput>
                    </FormItem>
                    <FormItem name="radio" label="">
                        <RadioGroup v-model="form1.radio">
                            <Radio label="man"><Icon type="man"></Icon></Radio>
                            <Radio label="woman"><Icon type="woman"></Icon></Radio>
                        </RadioGroup>
                    </FormItem>
                    <FormItem name="check" label="">
                        <CheckboxGroup v-model="form1.check">
                            <Checkbox value="1" label=""></Checkbox>
                            <Checkbox value="2" label=""></Checkbox>
                            <Checkbox value="3" label=""></Checkbox>
                            <Checkbox value="4" label=""></Checkbox>
                        </CheckboxGroup>
                    </FormItem>
                    <FormItem name="select" label="">
                        <FormSelect v-model="form1.select" placeholder="" style="width:300px;" bubble>
                            <FormOption :option="{value:'1',label:'1'}"></FormOption>
                            <FormOption :option="{value:'2',label:'2'}"></FormOption>
                            <FormOption :option="{value:'3',label:'3'}"></FormOption>
                        </FormSelect>
                    </FormItem>
                    <FormItem name="switch" label="">
                        <FormSwitch v-model="form1.switch"></FormSwitch>
                    </FormItem>
                    <FormItem name="switch" label="">
                        <Date 
                        type="date" 
                        v-model="form1.date"
                        placeholder="" 
                        style="width: 300px" 
                        ></Date>
                    </FormItem>
                    <FormItem name="rate" label="">
                        <Rate v-model="form1.rate" allow-half></Rate>
                    </FormItem>
                    <FormItem name="rate" label="">
                        <Upload
                            class="avatar-uploader"
                            action=""
                            :show-file-list="false"
                            v-model="form1.file"
                            :on-change="handleAvatarChange"
                            :auto-upload="false">
                            <img v-if="imageUrl" :src="imageUrl" class="avatar">
                            <Icon v-else type="plus" class="avatar-uploader-icon"></Icon>
                        </Upload>
                    </FormItem>
                    <FormItem name="textarea" label="">
                        <FormInput v-model="form1.textarea" placeholder="" type="textarea" :width="300"></FormInput>
                    </FormItem>
                    <FormItem>
                        <BaseButton type="primary" @on-click-btn="handleSubmit('form1')"></BaseButton>
                        <BaseButton type="ghost" style="margin-left: 8px" @on-click-btn="handleReset('form1')"></BaseButton>
                    </FormItem>
                </Form>
                {{form1}}
                <div class="q-title"></div>
                <div>
                    formformInputRadioCheckBoxSelectSwitchRateDateUpload
                    <p>rulesref<a href="https://github.com/yiminghe/async-validator">async-validator</a></p>
                </div>
                <div class="q-title"></div>
                <DCode :code="code.base" lang="html"></DCode>
            </div>
            <div class="title2" id="title-2">
                
            </div>
            <div class="wrap dynamic-form">
                <Form ref="form2" :value="form2" :label-width="80" style="width: 500px">
                    <FormItem
                            v-for="(item, index) in form2.items"
                            v-if="item.status"
                            :key="index"
                            :label="'Item ' + item.index"
                            :name="'items.' + index + '.value'"
                            :rules="{required: true, message: 'Item ' + item.index +' ', trigger: 'blur'}">
                        <Row>
                            <Col span="18">
                                <FormInput v-model="item.value" placeholder="" :width="300"></FormInput>
                            </Col>
                            <Col span="4">
                                <Icon type="minus-circled" @on-click-icon="handleRemove2(index)" size="24"></Icon>
                            </Col>
                        </Row>
                    </FormItem>
                    <FormItem>
                        <Row>
                            <Col span="12">
                                <Icon type="plus-circled" @on-click-icon="handleAdd2(index)" size="24"></Icon>
                            </Col>
                        </Row>
                    </FormItem>
                    <FormItem>
                        <BaseButton type="primary" @on-click-btn="handleSubmit('form2')"></BaseButton>
                        <BaseButton type="ghost" style="margin-left: 8px" @on-click-btn="handleReset('form2')"></BaseButton>
                    </FormItem>
                </Form>
                <div class="q-title"></div>
                <div>
                    
                </div>
                <div class="q-title"></div>
                <DCode :code="code.dyma" lang="html"></DCode>
            </div>
            <div class="title2" id="title-3">
                
            </div>
            <div class="wrap dynamic-form">
                <div>
                    <RadioGroup :value="labelPosition" type="button" @on-change="handleChangeP">
                        <Radio label="left">left</Radio>
                        <Radio label="top">top</Radio>
                        <Radio label="right">right</Radio>
                    </RadioGroup>
                </div>
                <br/>
                <Form ref="form2" :value="form2" :label-position="labelPosition" :label-width="80" style="width: 500px">
                    <FormItem
                            v-for="(item, index) in form2.items"
                            v-if="item.status"
                            :key="index"
                            :label="'Item ' + item.index"
                            :name="'items.' + index + '.value'"
                            :rules="{required: true, message: 'Item ' + item.index +' ', trigger: 'blur'}">
                        <Row>
                            <Col span="18">
                                <FormInput v-model="item.value" placeholder="" :width="300"></FormInput>
                            </Col>
                            <Col span="4">
                                <Icon type="minus-circled" @on-click-icon="handleRemove2(index)" size="24"></Icon>
                            </Col>
                        </Row>
                    </FormItem>
                    <FormItem>
                        <Row>
                            <Col span="12">
                                <Icon type="plus-circled" @on-click-icon="handleAdd2(index)" size="24"></Icon>
                            </Col>
                        </Row>
                    </FormItem>
                    <FormItem>
                        <BaseButton type="primary" @on-click-btn="handleSubmit2('form2')"></BaseButton>
                        <BaseButton type="ghost" style="margin-left: 8px" @on-click-btn="handleReset('form2')"></BaseButton>
                    </FormItem>
                </Form>
                <div class="q-title"></div>
                <div>
                    label-position
                </div>
                <div class="q-title"></div>
                <DCode :code="code.align" lang="html"></DCode>
            </div>
            <div class="title2" id="title-4">
                
            </div>
            <div class="wrap dynamic-form">
                <Form ref="form3" :value="form3" :rules="rule3" :label-width="80">
                    <FormItem label="" name="passwd">
                        <FormInput type="password" v-model="form3.passwd"></FormInput>
                    </FormItem>
                    <FormItem label="" name="passwdCheck">
                        <FormInput type="password" v-model="form3.passwdCheck"></FormInput>
                    </FormItem>
                    <FormItem label="" name="age">
                        <FormInput type="text" v-model="form3.age" number></FormInput>
                    </FormItem>
                    <FormItem>
                        <BaseButton type="primary" @on-click-btn="handleSubmit('form3')"></BaseButton>
                        <BaseButton type="ghost" style="margin-left: 8px" @on-click-btn="handleReset('form3')"></BaseButton>
                    </FormItem>
                </Form>
                <div class="q-title"></div>
                <div>
                    
                </div>
                <div class="q-title"></div>
                <DCode :code="code.custom" lang="html"></DCode>
            </div>
            <div class="title2" id="title-5">
                API
            </div>
            <div class="wrap">
                <div class="q-title">Fromprops</div>
                <Table type="bordered" :thead="api.thead" :data="api.data">
                </Table>
                <div class="q-title">Fromevents</div>
                <Table type="bordered" :thead="event.thead" :data="event.data">
                </Table>
                <div class="q-title">FromItemprops</div>
                <Table type="bordered" :thead="itemapi.thead" :data="itemapi.data">
                </Table>
            </div>
        </div>
        <DMenu></DMenu>
    </div>
</template>

<script>
import Code from '../../../code/component/form/form';
export default {
  data() {
    const validatePass = (rule, value, callback) => {
        if (value === '') {
            callback(new Error(''));
        } else {
            if (this.form3.passwdCheck !== '') {
                // 
                this.$refs.form3.validateField('passwdCheck');
            }
            callback();
        }
    };
    const validatePassCheck = (rule, value, callback) => {
        if (value === '') {
            callback(new Error(''));
        } else if (value !== this.form3.passwd) {
            callback(new Error('!'));
        } else {
            callback();
        }
    };
    const validateAge = (rule, value, callback) => {
        if (!value) {
            return callback(new Error(''));
        }
        // 
        setTimeout(() => {
            if (!Number.isInteger(value)) {
                callback(new Error(''));
            } else {
                if (value < 18) {
                    callback(new Error('18'));
                } else {
                    callback();
                }
            }
        }, 1000);
    };
    return {
        code: Code,
        form1:{
            name:'',
            radio:'',
            check:[],
            select:'',
            switch:true,
            date:'',
            rate:0,
            file:'',
            textarea:''
        },
        imgUrl:'',
        rules1:{
            name:[
                {required: true, message: '', trigger: 'blur' }
            ],
            radio: [
                { required: true, message: '', trigger: 'change' }
            ],
            check: [
                { required: true, type: 'array', min: 1, message: '', trigger: 'change' },
                { type: 'array', max: 2, message: '2', trigger: 'change' }
            ],
            select: [
                { required: true, message: '', trigger: 'change' }
            ],
            textarea: [
                { required: true, message: '', trigger: 'blur' },
                { type: 'string', min: 20, message: '20', trigger: 'blur' }
            ]
        },
        labelPosition:'left',
        index: 1,
        form2: {
            items: [
                {
                    value: '',
                    index: 1,
                    status: 1
                }
            ]
        },
        form3: {
            passwd: '',
            passwdCheck: '',
            age: ''
        },
        rule3: {
            passwd: [
                { validator: validatePass, trigger: 'blur' }
            ],
            passwdCheck: [
                { validator: validatePassCheck, trigger: 'blur' }
            ],
            age: [
                { validator: validateAge, trigger: 'blur' }
            ]
        },
        api:{
            thead:[
                {
                    column:'prop',
                    name:'',
                },
                {
                    column:'description',
                    name:'',
                },
                {
                    column:'type',
                    name:'',
                },
                {
                    column:'option',
                    name:'',
                },
                {
                    column:'default',
                    name:'',
                },
            ],
            data:[
                {
                    prop:'value',
                    description:'',
                    type:'Object',
                    option:'-',
                    default:'-'
                },
                {
                    prop:'rules',
                    description:'  async-validator',
                    type:'Object',
                    option:'-',
                    default:'-'
                },
                {
                    prop:'label-position',
                    description:'',
                    type:'String',
                    option:'left/top/right',
                    default:'-'
                },
                {
                    prop:'label-width',
                    description:'',
                    type:'Number',
                    option:'-',
                    default:'-'
                },
                {
                    prop:'show-message',
                    description:'',
                    type:'Boolean',
                    option:'-',
                    default:'true'
                },
                {
                    prop:'autocomplete',
                    description:' autocomplete  off  on',
                    type:'String',
                    option:'-',
                    default:'off'
                },
            ]
        },
        itemapi:{
            thead:[
                {
                    column:'prop',
                    name:'',
                },
                {
                    column:'description',
                    name:'',
                },
                {
                    column:'type',
                    name:'',
                },
                {
                    column:'option',
                    name:'',
                },
                {
                    column:'default',
                    name:'',
                },
            ],
            data:[
                {
                    prop:'name',
                    description:' model ',
                    type:'String',
                    option:'-',
                    default:'-'
                },
                {
                    prop:'label',
                    description:'',
                    type:'String',
                    option:'-',
                    default:'-'
                },
                {
                    prop:'label-width',
                    description:'',
                    type:'Number',
                    option:'-',
                    default:'-'
                },
                {
                    prop:'label-for',
                    description:' label  for  element-id  label ',
                    type:'String',
                    option:'-',
                    default:'-'
                },
                {
                    prop:'required',
                    description:'',
                    type:'Boolean',
                    option:'-',
                    default:'true'
                },
                {
                    prop:'rules',
                    description:'',
                    type:'Object | Array',
                    option:'-',
                    default:'-'
                },
                {
                    prop:'error',
                    description:', error',
                    type:'String',
                    option:'-',
                    default:'-'
                },
                {
                    prop:'show-message',
                    description:'',
                    type:'Boolean',
                    option:'-',
                    default:'true'
                },
            ]
        },
        event:{
            thead:[
                {
                    column:'name',
                    name:'',
                },
                {
                    column:'description',
                    name:'',
                },
                {
                    column:'props',
                    name:'',
                }
            ],
            data:[
                {
                    name:'validate',
                    description:' Boolean  Promise',
                    props:'callback'
                },
                {
                    name:'validateField',
                    description:'1 prop2',
                    props:'callback'
                },
                {
                    name:'resetFields',
                    description:'',
                    props:'-'
                }
            ]
        },
    };
  },
  methods: {
    handleAvatarChange(file,files){
        this.imageUrl = file.url;
    },
    handleSubmit (name) {
        this.$refs[name].validate((valid) => {

            if (valid) {
                console.log("success");
            } else {
                console.log("fail");
            }
        })
    },
    handleReset (name) {
        this.$refs[name].resetFields();
    },
    handleAdd2 () {
        this.index++;
        this.form2.items.push({
            value: '',
            index: this.index,
            status: 1
        });
    },
    handleRemove2 (index) {
        this.form2.items[index].status = 0;
    },
    handleChangeP(val){console.log("handleChangeP");console.log(val)
        this.labelPosition = val;
    }
  },
  mounted() {}
};
</script>
<style lang="scss">
.dynamic-form{
    .vsu-col-span-4{
        .vsu-ion{
            vertical-align:middle;
        }
    }
    .vsu-ion{
        cursor:pointer;
       &:hover{
           color:#2C41B0;
       }
    }
}
</style>
