let code = {}

code.formatComma = `
    /**
     * @
     * @param {float} value - .
     * @param {float} n - 2.
     * @param {int} t - nauto0
            n2isAutoauto:
            1.221.22
            1.201.2
            1.001   .
    *
    */
    Vue.filter('formatComma', function (s, n, isAuto, flag) {
        if (!s && s !== 0) return '-'
        if (!isNaN(s)) {
            n = n >= 0 && n <= 20 ? n : 2
            if (flag) {
                s = s < 10000 ? parseFloat((s + '').replace(/[^\d\.-]/g, '')).toFixed(0) + '' : parseFloat((s + '').replace(/[^\d\.-]/g, '')).toFixed(n) + ''
            } else {
                s = parseFloat((s + '').replace(/[^\d\.-]/g, '')).toFixed(n) + ''
            }
            var l = s.split('.')[0].split('').reverse(),
            r = s.split('.')[1]
            var t = ''
            for (var i = 0; i < l.length; i++) {
                t += l[i] + ((i + 1) % 3 == 0 && (i + 1) != l.length && (l[i + 1] !== '-') ? ',' : '')
            }
            if (isAuto && (isAuto === 'auto')) { // 0
                var rTemp = '0.' + r
                rTemp = parseFloat(rTemp) // 
                r = (rTemp) ? String(rTemp).split('.')[1] : ''
            }
            var res = t.split('').reverse().join('') + ((r) ? ('.' + r) : '')
            return res
        }
        return s
    })
`
code.formatRate = `
    /**
     * @
     * @param {float} value - .
     * @param {int} t - 2.
     * @param {int} u - %.
     *
     */
    Vue.filter('formatRate', function (value, t, u) {
        if (value === '-') {
            return value
        }
        if ((!value && value !== 0) || isNaN(value)) return '-'
        var unit = (u === -1) ? '' : '%'
        var isNegati = (value < 0) ? '-' : ''
        if (t === -1) {
            return isNegati + parseFloat(Math.abs(value * 100)) + unit
        }
        return isNegati + parseFloat(Math.abs(value * 100).toFixed(2)) + unit
    })
`


code.formatNum = `  
    /**
     * @
     * @param {float} num - .
     * @param {float} type - 
     * @param {int} toFixed - 
     *
     */
    Vue.filter('formatNum', function (num, type, toFixed, flag) {
        if (num === '-') {
            return type === 'unit' ? '' : num
        }
        var rate = 1
        var unit = ''
        if ((!num && num !== 0) || isNaN(num)) {
            if (type === 'unit') {
            return ''
            } else {
            return '-'
            }
        }
        if (num < 10000) {
            rate = 1
            unit = ''
        } else if (num > 99999999) {
            rate = 100000000
            unit = ''
        } else if (num > 9999999) {
            rate = 10000000
            unit = ''
        } else {
            rate = 10000
            unit = ''
        }
        // 
        var tf = toFixed === 0 || toFixed ? toFixed : 2
        // type,+
        if (!type) {
            return parseFloat((num / rate).toFixed(tf)) + unit
            // "value",
        } else if (type === 'value') {
            if (flag) {
            if (num < 10000) {
                return parseFloat((num / rate).toFixed(0))
            } else {
                return parseFloat((num / rate).toFixed(tf))
            }
            } else {
            return parseFloat((num / rate).toFixed(tf))
            }
        } else {
            //  "unit",
            return unit
        }
    })
`


code.formatFlow = `
    /**
     * @
     * @param {float} num - .
     * @param {string} type - 
     * @param {string} unit - T,G,M,K, M
     * @param {int} toFixed - 
     *
     */
    Vue.filter('formatFlow', function (num, type, unit, toFixed) {
        // -
        if (num === '-') {
            return type === 'unit' ? '' : num
        }
        if ((!num && num !== 0) || isNaN(num)) {
            return '-'
        }
        // 
        var rate = 1
        var finalUnit = ''
        // Mb
        if (unit) {
            switch (unit) {
            case 'K':
                num = num / 1024
                break
            case 'M':
                num = num * 1
                break
            case 'G':
                num = num * 1024
                break
            case 'T':
                num = num * 1024 * 1024
                break
            case 'E':
                num = num * 1024 * 1024 * 1024
                break
            }
        }
        // Eb
        if (num >= 1073741824) {
            rate = 1073741824
            finalUnit = 'Eb'
            // Tb
        } else if (num >= 1048576) {
            rate = 1048576
            finalUnit = 'Tb'
            // Gb
        } else if (num >= 1024) {
            rate = 1024
            finalUnit = 'Gb'
        } else {
            rate = 1
            finalUnit = 'Mb'
        }
        // 
        var tf = toFixed === 0 || toFixed ? toFixed : 2
        // type,+
        if (!type) {
            return parseFloat((num / rate).toFixed(tf)) + finalUnit
            // "value",
        } else if (type === 'value') {
            return parseFloat((num / rate).toFixed(tf))
        } else {
            //  "finalUnit",
            return finalUnit
        }
    })
`


code.formatTime = `  
    /**
     * @
     * @param {float} num - .
     * @param {string} type - 
     * @param {string} unit - M(),H,D, H
     * @param {int} toFixed - 
     *
     */
    Vue.filter('formatTime', function (num, type, unit, toFixed) {
        // -
        if (num === '-') {
            return type === 'unit' ? '' : num
        }
        if ((!num && num !== 0) || isNaN(num)) {
            return '-'
        }
        // 
        var rate = 1
        var finalUnit = ''
        // Mb
        if (unit) {
            switch (unit) {
            case 'M':
                num = num / 60
                break
            case 'H':
                num = num * 1
                break
            case 'D':
                num = num * 24
                break
            }
        }
        // 
        if (num >= 24) {
            rate = 24
            finalUnit = ''
            // Tb
        } else if (num >= 1) {
            rate = 1
            finalUnit = ''
            // Gb
        } else {
            rate = 1 / 60
            finalUnit = ''
        }
        // 
        var tf = toFixed === 0 || toFixed ? toFixed : 2
        // type,+
        if (!type) {
            return parseFloat((num / rate).toFixed(tf)) + finalUnit
            // "value",
        } else if (type === 'value') {
            return parseFloat((num / rate).toFixed(tf))
        } else {
            //  "finalUnit",
            return finalUnit
        }
    })
`



export default code
