                </TagGroup>
                <div class="q-title"></div>
                <div>
                    closableon-close
                </div>
                <div class="q-title"></div>
                <DCode :code="code.close" lang="html"></DCode>
            </div>
            <div class="title2" id="title-3">
                
            </div>
            <div class="wrap">
                <TagGroup :tags="tag1">
                    <Tag v-for="(item,index) in tag1" :key="index" :tag="item" color="green">
