            <div class="title2" id="title-4">
                
            </div>
            <div class="wrap">
                <TagGroup :tags="tag1">
                    <Tag v-for="(item,index) in tag1" :key="index" :tag="item" :size="item.size">
                        {{item.name}}
                    </Tag>
                </TagGroup>
                <div class="q-title"></div>
                <div>
                    tagsize
                </div>
                <div class="q-title"></div>
                <DCode :code="code.size" lang="html"></DCode>
            </div>
            <div class="title2" id="title-5">
                
