                <TagGroup :tags="tag1" deraction="vertical" style="width:70px;">
                    <Tag v-for="(item,index) in tag1" :key="index" :tag="item">
                        {{item.name}}
                    </Tag>
                </TagGroup>
                <div class="q-title"></div>
                <div>
                    tagderaction
                </div>
                <div class="q-title"></div>
                <DCode :code="code.direction" lang="html"></DCode>
            </div>
            <div class="title2" id="title-6">
                API
            </div>
            <div class="wrap">
                <div class="q-title">Tagprops</div>
                <Table type="bordered" :thead="api.thead" :data="api.data">
                </Table>
                <div class="q-title">Tagevents</div>
                <Table type="bordered" :thead="event.thead" :data="event.data">
                </Table>
                <div class="q-title">Tagslot</div>
                <Table type="bordered" :thead="slots.thead" :data="slots.data">
                </Table>
                <div class="q-title">TagGroupprops</div>
                <Table type="bordered" :thead="groupapi.thead" :data="groupapi.data">
                </Table>
            </div>
        </div>
        <DMenu></DMenu>
    </div>
</template>

<script>
import Code from '../../../code/component/data/tag';
export default {
  data() {
    return {
        code: Code,
        tag1:[
            {
                id:'1',
                name:'',
                size:'large'
            },
            {
                id:'2',
                name:'',
                size:'default'
            },
            {
                id:'3',
                name:'',
                size:'small'
            },
            {
                id:'4',
                name:'',
                size:'small'
            }
        ],
        tag2:[
            {
                id:'1',
                name:''
            },
            {
                id:'2',
                name:''
            },
            {
                id:'3',
                name:''
            },
            {
                id:'4',
                name:''
            }
        ],
        api:{
            thead:[
                {
                    column:'prop',
                    name:'',
                },
                {
                    column:'description',
                    name:'',
                },
                {
                    column:'type',
                    name:'',
                },
                {
                    column:'option',
                    name:'',
                },
                {
                    column:'default',
                    name:'',
                },
            ],
            data:[
                {
                    prop:'tag',
                    description:'tag',
                    type:'String, Number, Object',
                    option:'-',
                    default:'-'
                },
                {
                    prop:'closable',
                    description:'',
                    type:'Boolean',
                    option:'-',
                    default:'false'
                },
                {
                    prop:'icon',
                    description:'',
                    type:'String',
                    option:'-',
                    default:'ios-close-empty'
                },
                {
                    prop:'color',
                    description:'',
                    type:'String',
                    option:'-',
                    default:'-'
                },
                {
                    prop:'size',
                    description:'',
                    type:'String',
                    option:'default/large/small',
                    default:'default'
                }
            ]
        },
        event:{
            thead:[
                {
                    column:'name',
                    name:'',
                },
                {
                    column:'description',
                    name:'',
                },
                {
                    column:'props',
                    name:'',
                }
            ],
            data:[
                {
                    name:'on-close',
                    description:'',
                    props:''
                }
            ]
        },
        slots:{
            thead:[
                {
                    column:'name',
                    name:'',
                },
                {
                    column:'description',
                    name:'',
                },
            ],
            data:[
                {
                    name:'',
                    description:'',
                }
            ]
        },
        groupapi:{
            thead:[
                {
                    column:'prop',
                    name:'',
                },
                {
                    column:'description',
                    name:'',
                },
                {
                    column:'type',
                    name:'',
                },
                {
                    column:'option',
                    name:'',
                },
                {
                    column:'default',
                    name:'',
                },
            ],
            data:[
                {
                    prop:'tags',
                    description:'tag',
                    type:'Array',
                    option:'-',
                    default:'[]'
                },
                {
                    prop:'deraction',
                    description:'',
                    type:'String',
                    option:'horizontal, vertical',
                    default:'horizontal'
                },
                {
                    prop:'size',
                    description:'',
                    type:'String',
                    option:'default/large/small',
                    default:'default'
                }
            ]
        },
    };
  },
  methods: {
    closeTag (event,tag) {
        let closeIndex;
        for(var i = 0;i<this.tag2.length;i++){
            if(this.tag2[i].name===tag.name){
                closeIndex = i;
            }
        }
        if(closeIndex!==undefined){
            this.tag2.splice(closeIndex,1);
            this.$emit('on-close-tag', event);
        }
    }
  },
  mounted() {}
};
</script>
